
function asyncComponent(path) {
	return () =>
		import('@/' + path + '.vue')
}

export default new VueRouter({
	mode: 'hash',
	routes: [{
			path: '*',
			component: asyncComponent('components/404'),
			meta: {
				title: '404'
			}
		},
		{
			path: '/test',
			component: asyncComponent('components/test'),
		},
		{
			path: '/',
			component: asyncComponent('pages/index/views/main'),
			children: [{
					path: '/nowTrends',
					component: asyncComponent('pages/index/views/nowTrends'),
					name: 'nowTrends'
				},
				{
					path: '/market',
					component: asyncComponent('pages/index/views/market'),
					name: 'market'
				},
				{
					path: '/basic',
					component: asyncComponent('pages/index/views/basic'),
					name: 'basic'
				},
				{
					path: '/splitting',
					component: asyncComponent('pages/index/views/splitting'),
					name: 'splitting'
				},

			]
		},


	],
})
